import { isDomainList } from "../../../src/core/url/shared";
import { isValidLicenseState } from "./license";
import { createSafeOriginUrl } from "./url";
const validDateRegExp = /^\d{4}-\d{1,2}-\d{1,2}$/;
export const isNumeric = (param) => typeof param === "number" && !Number.isNaN(param);
export const isNotEmpty = (param) => typeof param === "string" && param.length > 0;
export const isEmptyOrPositiveNumber = (param) => typeof param === "undefined" || (typeof param === "number" && param > 0);
export const isValidLicenseStates = (param) => {
    if (!param) {
        return true;
    }
    if (typeof param !== "string") {
        return false;
    }
    const licenseStates = param.split(",");
    return licenseStates.every((state) => isValidLicenseState(state));
};
export const isSafeUrl = (param) => {
    if (typeof param !== "string") {
        return false;
    }
    const url = createSafeOriginUrl(param);
    return typeof url === "string";
};
export function validateParams(command, paramDefinitions) {
    return paramDefinitions
        .map((definition) => {
        const name = String(definition.name);
        const param = command[name];
        return definition.validate(param)
            ? ""
            : `Invalid value for parameter "${name}", got "${param}":`;
    })
        .filter((result) => result !== "");
}
export const isValidDomainList = (param) => {
    if (!param) {
        return true;
    }
    if (typeof param !== "string") {
        return false;
    }
    return isDomainList(param);
};
export const isValidDate = (param) => {
    if (!param) {
        return false;
    }
    if (typeof param !== "string") {
        return false;
    }
    if (!validDateRegExp.test(param)) {
        return false;
    }
    if (!Date.parse(param)) {
        return false;
    }
    return true;
};
